<template>
  <div class="min-h-screen flex items-center justify-center bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
            <div class="max-w-md w-full space-y-8">
                <div>
                    <div>
                        
                        <img class="mx-auto h-48 w-auto" src="/static/logo.svg" alt="Logo" />
                        
                        <h2 class="text-center text-3xl font-extrabold text-gray-900">
                            Register for a new account
                        </h2>
                        <p class="mt-2 text-center text-sm text-gray-600">
                            Or <a href="/login" class="font-medium text-green-700 hover:text-green-600">login to your account </a>
                        </p>
                    </div>
                    <form class="mt-8 space-y-6" method="POST" @submit="checkForm">
                        <div class="rounded-md shadow-sm">
                            <div class="mt-5 mb-5">
                                <label htmlFor="name" class="sr-only">Name</label>
                                <input id="name" name="name" v-model="name" type="text" autoComplete="name" required class="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-md focus:outline-none focus:ring-green-600 focus:border-green-600 focus:z-10 sm:text-sm" placeholder="Name" />
                            </div>
                            <div class="mt-5 mb-5">
                                <label htmlFor="email-address" class="sr-only">Email address</label>
                                <input id="email-address" name="email" v-model="email" type="email" autoComplete="email" required class="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-md focus:outline-none focus:ring-green-600 focus:border-green-600 focus:z-10 sm:text-sm" placeholder="Email address" />
                            </div>
                            <div class="mt-5 mb-3">
                                <label htmlFor="password" class="sr-only">Password</label>
                                <input id="password" name="password" v-model="password" type="password" minLength="8" autoComplete="current-password" required class="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-md focus:outline-none focus:ring-green-600 focus:border-green-600 focus:z-10 sm:text-sm" placeholder="Password" />
                            </div>
                            <div class="mt-3 mb-3">
                                <label htmlFor="vpassword" class="sr-only">Confirm Password</label>
                                <input id="vpassword" name="vpassword" v-model="vpassword" type="password" minLength="8" autoComplete="current-password" required class="appearance-none rounded-none relative block w-full px-3 py-2 border border-gray-300 placeholder-gray-500 text-gray-900 rounded-md focus:outline-none focus:ring-green-600 focus:border-green-600 focus:z-10 sm:text-sm" placeholder="Confirm Password" />
                            </div>

                            <p v-if="errors.length">
                              <b class="text-center text-l font-bold text-gray-900">Please correct the following error(s):</b>
                              <ul>
                                <li v-for="(error, index) in errors" :key="index">{{ error }}</li>
                              </ul>
                            </p>
                        </div>

                        <div class="flex items-center justify-end"> <!-- justify-between -->
                            <div class="text-sm">
                                <a href="/recover" class="font-medium text-green-700 hover:text-green-600">
                                    Forgot your password?
                                </a>
                            </div>
                        </div>

                        <div>
                            <button type="submit" class="group relative w-full flex justify-center py-2 px-4 border border-transparent text-sm font-medium rounded-md text-white bg-green-700 hover:bg-green-600 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
                                Register
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
</template>

<script>



export default {
  name: 'App',
  data() {
    return {
      errors: [],
      name: null,
      email:null,
      password: null,
      vpassword: null
    }
  },
  methods: {
    checkForm: function (e) {

      this.errors = [];

      if (this.password !== this.vpassword) {
        this.errors.push("The two passwords must be identical")
      }
      
      if (!this.errors.length) {
        return true;
      }

      e.preventDefault();
    }
  }
}
</script>


